import React from 'react'
import { useSelector } from 'react-redux'
//import lodash from 'lodash'


const Users = () => {

  console.log('BLAAAA')
  const blogs = useSelector(state => state.blogs)
  console.log(blogs)

  const numberOfBlogs = blogs.map(b => b.username)
  console.log(numberOfBlogs)

  /*
  const numberOfBlogs = [
    {
      username: 'Aatami',
      blogs: 3,
      key: 0
    },
    {
      username: 'Btami',
      blogs: 100,
      key: 1
    }
  ]
  */

  console.log(numberOfBlogs)

  return (
    <div>
      <h1>Users</h1>
      <ul>
        {numberOfBlogs.map(b =>
          <li key={b.key}>
            {b.username} {b.blogs}
          </li>)}
      </ul>
    </div>
  )
}

export default Users