import blogService from '../services/blogs'
import { changeToneToPos } from '../reducers/toneReducer'
import { addNotification, dropNotification } from '../reducers/notificationReducer'

const blogReducer = (state = [], action) => {
  switch (action.type) {
  case 'ADD_BLOG':
    return state.concat(action.newBlog)
  case 'REMOVE_BLOG':
    return state.filter(b => b.id !== action.id)
  case 'CHANGE_BLOG':
    return state.map(b =>
      b.id !== action.changedBlog.id
        ? b
        : action.changedBlog
    )
  case 'INIT_BLOGS':
    return action.data
  default:
    return state
  }
}

export const addBlog = (blogObject) => {
  return async dispatch => {
    const newBlog = await blogService.create(blogObject)
    dispatch(changeToneToPos())
    dispatch(addNotification(`A new blog "${newBlog.title}" added`))
    setTimeout(() => {
      dispatch(dropNotification())
    }, 5000)
    dispatch({
      type: 'ADD_BLOG',
      newBlog
    })
  }
}


export const initBlogs = () => {
  return async dispatch => {
    const data = await blogService.getAll()
    console.log('Palautuuko dataa??')
    console.log(data)
    dispatch({
      type: 'INIT_BLOGS',
      data
    })
  }
}

export const changeBlog = (blogObject) => {
  return async dispatch => {
    const changedBlog = await blogService.change(blogObject)
    dispatch({
      type: 'CHANGE_BLOG',
      changedBlog
    })
  }
}

export const removeBlog = (id) => {
  return async dispatch => {
    await blogService.remove(id)
    dispatch({
      type: 'REMOVE_BLOG',
      id
    })
  }
}



export default blogReducer